import clsx from "clsx";
import React, { useState } from "react";
import "../../styles/tailwind.css";

export interface ButtonProps {
  /** Is this the principal call to action on the page? */
  primary?: boolean;
  /** What background color to use */
  backgroundColor?: string;
  /** How large should the button be? */
  size?: "small" | "medium" | "large";
  /** Button contents */
  label: string;
  /** Optional click handler */
  onClick?: () => void;
  rounded?: boolean;
  className?: string;
  color?: string;
}

/** Primary UI component for user interaction */
export const Button = ({
  primary = false,
  size = "medium",
  backgroundColor,
  label,
  rounded,
  className,
  color,
  ...props
}: ButtonProps) => {
  const [hover, setHover] = useState(false);

  console.log("hover", hover);

  return (
    <button
      onMouseOver={() => {
        setHover(true);
      }}
      onMouseUp={() => {
        setHover(true);
      }}
      onMouseLeave={() => {
        setHover(false);
      }}
      type="button"
      className={`${className} border-solid border-2 px-3 py-1 border-main-color text-main-color ${clsx(
        {
          [`text-[${color}]`]: color,
          ["rounded"]: rounded,
          ["w-full"]: size === "large",
          ["w-full max-w-[600px]"]: size === "medium",
        }
      )}`}
      style={{ backgroundColor }}
      {...props}
    >
      {label}
    </button>
  );
};
